.header{
  color:  #398fa6;
  width: 100%;
  text-align: center;
  height: 170px;
  padding: 30px ;
}

.logo
{
  color:  #3f81a3;
  margin: 10px;
  font-family: "Lucida Handwriting", sans-serif;
}

.main-letter
{
  padding: 1px;
  border-color: #b6d8e4;
  border-style: solid;
  border-width: 4px;
  display: inline-block;
  font-size: 25px;
  margin: 0;
}

.word
{
  margin: 0;
  padding: 0;
  display: inline-block;
  font-size: 35px;
}

.header-title{
  font-size: 50px;
}

.header-text{
  font-size: 20px;
}

.section{
  flex-flow: row wrap;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.section > div {
  padding: 30px;
}

.small-div {
  width: 20%;
}

.wide-image
{
  max-width: 300px;
}

.vert-image
{
  max-width: 200px;
}

.big-div {
  width: 100%;
}

.big-div-art {
  width: 80%;
}

.bg-grey{
  background-color:#f6f6f6;
}

.feature
{
  display: inline-block;
  text-align: center;
  color:  #3f81a3;
  cursor: pointer;
  margin: 5px;
}

.div-title {
  font-size: 30px;
  font-weight: 600;
  color: #3f81a3;
}

.title-statement {
  font-size: 50px;
  font-weight: 600;
  color: #3f81a3;
  text-align: center;
}

.problem {
  font-size: 30px;
  font-weight: 600;
  background-color: #3f81a3;
  color: white;
  text-align: center;
  padding: 10px;
}

.input-email
{
  padding: 15px;
  box-shadow: 0px 5px 35px 0px rgba(50, 50, 93, 0.17);
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
}

.email-subs
{
  width: 80%;
  display: inline-block;
  border: 0;
  background-color: transparent;
  margin: 0;
  padding: 0;
}

.button-subs
{
  display: inline-block;
  color: #3f81a3;
  font-weight: 800;
  cursor: pointer;
  border: 0;
  background-color: transparent;
  margin: 0;
  padding: 0;
}

.problem-content
{
  text-align: center;
  font-weight: 600;
}

.problem-card
{
  display: inline-block;
  width: 200px;
  margin: 10px;
  text-align: center;
  color:  #3f81a3;
}
.problem-card img
{
  max-width: 200px;
}

@media only screen and (min-width:320px) and (max-width:725px){
  .contact-form{
    display: flex;
    flex-direction: column;
    width:80%;
  }

  .section{

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }


  .small-div {
    width: 80%;
  }
}